<template>
  <div style="width: 100%; height: 100%;">
    <el-dialog title="目录" :visible.sync="mode.isCatalogVisible" width="70%" center>
      <el-menu :default-active="async_name" background-color="#545c64"
               text-color="#fff"
               active-text-color="#ffd04b">
        <el-submenu v-for="(value0, key0) in catalog_data" :key="key0" :index="key0">
          <template slot="title">{{key0}}</template>
          <el-submenu v-for="(value1, key1) in value0" :key="key1" :index="key0+key1">
            <template slot="title">{{key1}}</template>
            <el-menu-item v-for="item in value1" :key="item" :index="`${key0}/${key1}/${item}`"
                          @click="goTo(key0, key1, item)">{{item}}
            </el-menu-item>
          </el-submenu>
        </el-submenu>
      </el-menu>
    </el-dialog>
    <h1 class="top">{{title}}</h1>
    <div id="editor_control" style="padding: 10px;">
      <el-button-group>
        <!--<el-button icon="el-icon-back" title="返回" @click="$router.back(-1)"></el-button>-->
        <el-tooltip effect="dark" content="目录" placement="top">
          <el-button icon="el-icon-s-fold" @click="mode.isCatalogVisible = !mode.isCatalogVisible"></el-button>
        </el-tooltip>
        <!--<el-button icon="el-icon-moon" v-if="mode.isWhite" title="背景黑/白"-->
        <!--@click="mode.isWhite = !mode.isWhite"></el-button>-->
        <!--<el-button icon="el-icon-sunny" v-else title="背景黑/白" @click="mode.isWhite = !mode.isWhite"></el-button>-->
        <el-tooltip effect="dark" content="代码显/隐" placement="top">
          <el-button icon="el-icon-view" @click="mode.isCodeShow = !mode.isCodeShow"></el-button>
        </el-tooltip>
        <el-tooltip effect="dark" content="代码横/竖" placement="top">
          <el-button icon="el-icon-copy-document"
                     @click="mode.isHorizontal = !mode.isHorizontal"></el-button>
        </el-tooltip>
        <el-tooltip effect="dark" content="运行代码" placement="top">
          <el-button icon="el-icon-video-play" @click="runCode"></el-button>
        </el-tooltip>
      </el-button-group>
    </div>
    <div id="demo_container" :class="{code_hide: !mode.isCodeShow, vertical: !mode.isHorizontal}">
      <div v-show="mode.isCodeShow" id="editor">
        <textarea ref="mytext">这里是默认内容</textarea>
      </div>
      <div id="iframe">
        <iframe id="myframe" src=""></iframe>
      </div>
    </div>
  </div>
</template>
<script>
  import('./lib/highlight/default.min.css')
  //const catalog_data = () => import('./catalog.js')
  import catalog_data from './catalog.js'

  export default {
    data () {
      return {
        mode: {
          isCatalogVisible: false,
          isWhite: true,
          isHorizontal: true,
          isCodeShow: true,
        },
        catalog_data: {},
        title: '',
        async_name: '',
      }
    },
    computed: {},
    components: {},
    created () {
      console.log(this)
      this.catalog_data = catalog_data
      this.title = this.$route.path.split('/')[this.$route.path.split('/').length - 1]
      this.async_name = this.$route.path.split('/demo/')[1]
      this.mode.isCatalogVisible = this.$route.path.endsWith('/demo/') ? true : false
    },
    mounted () {
      this.async_getCode(this.async_name)
    },
    methods: {
      async_getCode (path) {
        if (path) {
          import(`./${path}.js`).then(code => {
            this.$nextTick(() => {
              this.$refs.mytext.value = code.default
              this.title = this.$route.path.split('/')[this.$route.path.split('/').length - 1]
              this.runCode()
            })
          }).catch((err) => {
            console.log(err)
            //this.$router.push('/404')
          })
        }
      },
      goTo (...args) {
        let path = args.join('/')
        this.$router.push(`/demo/${path}`)
        this.async_getCode(path)
        this.mode.isCatalogVisible = false
      },
      runCode () {
        //let code = '<script type="text/javascript" src="/static/js/demo_share.js"><\/script>' + this.$refs.mytext.value
        let code = `<script type="text/javascript">
                function write (...args) {
                    args.forEach((item, index) => {
                        document.write(item)
                        index < args.length-1 ? document.write(', ') : document.write('<br/>')
                    })
                }
            <\/script>` + this.$refs.mytext.value
        let myframe = document.getElementById('myframe')
        myframe.contentWindow.document.open()
        myframe.contentWindow.document.write(code)
        myframe.contentWindow.document.close()
      },
    }
  }
</script>

<style scoped lang="less">
  .top {
    text-align: center;
    height: 50px;
    margin: 0;
  }

  textarea {
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 100%;
    font-size: 0.8rem;
  }

  iframe {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: 1px solid gray;
  }

  #demo_container {
    position: absolute;
    top: 100px;
    right: 10px;
    bottom: 10px;
    left: 10px;

    #editor {
      width: 50%;
      height: 100%;
      position: absolute;
      left: 0;
      box-sizing: border-box;
      padding: 0 5px 0 0;
    }

    #iframe {
      width: 50%;
      height: 100%;
      position: absolute;
      right: 0;
      box-sizing: border-box;
      padding: 0 0 0 5px;
    }

    &.code_hide {
      #editor {
        padding: 0;
      }

      #iframe {
        width: auto;
        left: 0;
        padding: 0;
      }
    }

    &.vertical {
      #editor {
        width: 100%;
        height: 50%;
        position: absolute;
        top: 0;
        box-sizing: border-box;
        padding: 0 0 5px 0;
      }

      #iframe {
        width: 100%;
        height: 50%;
        position: absolute;
        bottom: 0;
        box-sizing: border-box;
        padding: 5px 0 0 0;
      }
    }

    &.vertical.code_hide {
      #iframe {
        width: auto;
        height: 100%;
        left: 0;
        padding: 0;
      }
    }
  }
</style>



